<template>
  <div class="vote">
    <p id="head">LA<br>NUIT<br>GLAMOUR</p>
    <p id="sub-head">The Official GESA Dinner</p>

    <div class="container" v-if="hasVoted === 'false'">
      <p id="info">
        Please vote for a person from each category
      </p>

      <div class="vote-category" v-for="(vo) in voteOptions" :key="vo.catId" :data-category-id="vo.catId">
        <h4 class="vote-category-title"><span>Category: </span>{{vo.category}}</h4>
        <ul class="vote-options">
          <li class="vote-option" v-for="(o) in vo.options" :key="o.id" :data-candidate-id="o.id" @click="onCandidateSelect(o.id, vo.catId)">
            {{o.name}}
          </li>
        </ul>
      </div>

      <hr class="divider">
      <button id="vote-btn" @click="onVote">VOTE</button>
    </div>
    <div id="thanks-msg" v-else>
      Thank you for voting. You can vote only once!
    </div>

  </div>
</template>

<script>
import firebase from 'firebase/app'
import 'firebase/firestore'

let db = firebase.firestore()

export default {
  data: () => ({
    voteOptions: [],
    myVote: {},
    hasVoted: 'false',
  }),
  methods: {
    onCandidateSelect(id, cid){
      // deselect all options
      let c = document.querySelector(`[data-category-id="${cid}"]`)
      let l = c.querySelectorAll('.vote-options > .vote-option')
      for(let i = 0; i < l.length; i++){
        l[i].classList.remove('selected');
      }
      // select the selected option
      document.querySelector(`[data-candidate-id="${id}"]`).classList.add('selected')
      // reflect in data
      this.myVote[cid] = id;
    },
    onVote(){
      // mark computer as voted
      window.localStorage.setItem('has-voted', 'true')
      this.hasVoted = true
      // submit vote
      Object.keys(this.myVote).forEach((k, i) => {
        db.runTransaction((t) => {
          return t.get(db.collection(k).doc(this.myVote[k]))
            .then((d) => {
              if(!d) throw `document "${k} -> ${this.myVote[k]}" does not exist`
              t.update(db.collection(k).doc(this.myVote[k]), {votes: d.data().votes + 1})
            })
        })
      })
    },
    _loadDepartmentalVotes(){
      let o = {
        catId: 'departmental-votes',
        category: 'Best Departmental President',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      // console.log(o);
      this.voteOptions.push(o)
    },
    _loadDiscoveryVotes(){
      let o = {
        catId: 'discovery-votes',
        category: 'Discovery of the Year',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      this.voteOptions.push(o)
    },
    _loadEntrepreneurVotes(){
      let o = {
        catId: 'entrepreneur-votes',
        category: 'Student Entrepreneur of the Year',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      this.voteOptions.push(o)
    },
    _loadFemaleVotes(){
      let o = {
        catId: 'female-votes',
        category: 'Best Dressed Female Student',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      this.voteOptions.push(o)
    },
    _loadInfluentialVotes(){
      let o = {
        catId: 'influential-votes',
        category: 'Most Influential',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      this.voteOptions.push(o)
    },
    _loadInternationalVotes(){
      let o = {
        catId: 'international-votes',
        category: 'Most Outstanding International Student',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      this.voteOptions.push(o)
    },
    _loadMaleVotes(){
      let o = {
        catId: 'male-votes',
        category: 'Best Dressed Male Student',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      this.voteOptions.push(o)
    },
    _loadVersatileVotes(){
      let o = {
        catId: 'versatile-polls',
        category: 'Most Versatile/Innovative',
        options: [],
      }
      db.collection(o.catId).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          let t = {
            id: doc.id,
            name: doc.get('name'),
          }
          o.options.push(t)
        });
      });
      this.voteOptions.push(o)
    },
  },
  created(){
    // check if voted
    this.hasVoted = window.localStorage.getItem('has-voted') || 'false'
    if(this.hasVoted === 'true')
      return
    // get vote options
    this._loadInfluentialVotes()
    this._loadDiscoveryVotes()
    this._loadVersatileVotes()
    this._loadEntrepreneurVotes()
    this._loadDepartmentalVotes()
    this._loadMaleVotes()
    this._loadFemaleVotes()
    this._loadInternationalVotes()
  },
}
</script>

