  margin: 24px 16px;
  /* display: flex; */
  /* flex-flow: column nowrap; */
  /* align-items: center; */
}
.vote-category{
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}
.vote-category-title{
  text-align: right;
  align-self: flex-end;
  color: #e19e1f;
  font-size: 1.3em;
  font-style: italic;
  margin-bottom: 8px;
}
.vote-category-title > span{
  font-size: 0.6em;
  /* font-variant: small-caps; */
  color: #bbb;
}
