  list-style: none;
  font-size: 1.2em;
  padding: 0px;
  user-select: none;
}
.vote-option{
  width: 100%;
  border: 1px solid #e19e1f;
  border-radius: 8px;
  cursor: pointer;
  margin: 4px 0px;
  padding: 4px;
}
.vote-option.selected{
  background-color: #e19e1f;
